body {
  margin: 0;
  font-family: "Titillium Web", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  background-color: #333;
  margin-top: 0;
  padding: 0 0;
  font-size: 20px;
  color: white;
  list-style-type: none;
  display: flex;
  justify-content: space-around;
}
.liItem {
  width: 50%;
  padding: 20px;
  text-align: center;
  border-left: 1px solid white;
  border-right: 1px solid white;
  transition: all 0.4s ease 0s;
}
.liItem:hover {
  cursor: pointer;
  background: tomato;
  color: #333;
  transition: all 0.4s ease 0s;
}

/* breed list */
.breedListing {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: Center;
  padding: 0;
}

.breedListingItem {
  text-align: center;
  padding: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  margin: 5px;
  width: 280px;
  height: 280px;
}
.breedListingLink {
  color: Black;
  text-decoration: none;
  font-style: italic;
}

/* random image style */

.randomImgStl {
  width: 300px;
  border-radius: 50%;
  height: 300px;
}

/* Intro page style */

.btnStyle {
  color: white;
  text-decoration: none;
  background: #333;
  padding: 20px;
  display: inline-block;
  transition: all 0.4s ease 0s;
  border: none;
}

.btnStyle:hover {
  color: #333;
  background: tomato;
  transition: all 0.4s ease 0s;
}
